@import "../styles/colors";

.skills {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: .2rem;
  grid-column-gap: .5rem;
  justify-self: stretch;

  margin: 0 1rem;

  label {
    grid-column: 1;
    text-align: right;
  }
  .score-bar {
    grid-column: 2;
    background-color: $snowWhite;
    height: 1rem;

    .score-bar__score {
      background-color: $snowShadow;
      height: 1rem;

      span {
        display: none;
      }

      @media print {
        span {
          display: inherit;
        }
      }
    }
  }

}
