@import "../styles/colors";

#mycard--experience {
  h3 {
    padding-top: .2rem;
    margin: .5rem 0;
    border-top: .1rem solid;
    border-image: linear-gradient(to right, $snowMan, $snowWhite) 100;
  }
}

.App--dark {
  #mycard--experience {
    h3 {
      border-image: linear-gradient(to left, $snowMan, $snowWhite) 100;
    }
  }
}
img.company-icon {
  height: 1rem;
  margin-right: 0.5rem;
  display: inline;
}

.technology-list {
  display: flex;
  flex-flow: row wrap;

  li {
    margin-right: 2rem;
  }
}